import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './App.css';
import Coin from './Coin';
import img from './GOOGL.png';
import img1 from './FB.png';
import img2 from './AMZN.svg';



function App() {
  const [coins, setCoins] = useState([]);
  const [search, setSearch] = useState('');

  

  useEffect(() => {
    axios
      .get(
        'https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1&sparkline=false'
      )
      .then(res => {
        setCoins(res.data);
        console.log(res.data);
      })
      .catch(error => console.log(error));
  }, []);

  const handleChange = e => {
    setSearch(e.target.value);
  };

  const filteredCoins = coins.filter(coin =>
    coin.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div>
      <div className="quick1">
       <h1 className="quick">Quikie</h1>
       <p className="p"> App</p>
       </div>
       <div className="card">
       <div className="photo">
        <img className="photo1" src={img}></img>
        <p className="google">GOOGL</p>
        <h4>1515 USD </h4>
       </div>
       <div className="photoA">
        <img className="photo1" src={img1}></img>
        <p className="google">FB</p>
        <h4>266 USD </h4>
       </div>
       <div className="photoB">
        <img className="photo1" src={img2}></img>
        <p className="google">AMZN</p>
        <h4>3116 USD </h4>
       </div>
       </div>
    <div className='coin-app'>
      
      <div className='coin-search'>
        <h1 className='coin-text'>Search a currency</h1>
        <form>
          <input
            className='coin-input'
            type='text'
            onChange={handleChange}
            placeholder='Search'
          />
        </form>
      </div>
      {filteredCoins.map(coin => {
        return (
          <div className="border">
          <Coin
            key={coin.id}
            name={coin.name}
            price={coin.current_price}
            symbol={coin.symbol}
            marketcap={coin.total_volume}
            volume={coin.market_cap}
            image={coin.image}
            priceChange={coin.price_change_percentage_24h}
          /></div>
        );
      })}
    </div>
    </div>
  );
}

export default App;

